.twoImages {
    display: flex;
    justify-content: space-between;

    &__item {
        width: col(6,12);
        height: 0;
        padding-top: 54.474708171%;
        position: relative;
        & + &{
            margin-left: gutter(12);
        }
        img {
            height: 100%;
            width: 100%;
            position: absolute;
            top: 0;
            bottom: 0;
            right: 0;
            left: 0;
            object-fit: cover;
            display: block;
        }
    }

    @media screen and (max-width: 600px) {
        flex-direction: column;
        &__item{
            width: 100%;
            padding-top: 700 / 625 * 100%;

            & + &{
                margin-left: 0;
                margin-top: s(30);
            }

        }
    }

}



